nav{
    background-color: black;
    padding: 0.5rem;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

nav img{
    width: 100%;
}

.item{
    color: white;
    text-decoration: none;
    padding: 0 1rem;
    font-size: 20px;
}

.item:hover{
    box-shadow: 0px 1px white;
    color: aqua;
    font-weight: bolder;
}